$header-height: 70px;

.App {
  text-align: center;
}

.title {
  font-family: "Google Sans", Roboto, Arial, sans-serif;
  font-size: 30px;
  margin: 0;
  color: #ffffff;
}

.main {
  height: calc(100vh - $header-height - 50px);
  position: relative;
}

.main__body {
  width: 100%;
  display: grid;
  grid-template-columns: 2fr 8fr;
  grid-template-rows: calc(100vh - $header-height);
  margin: 0 auto;
}

.menu {
  height: calc(100vh - $header-height);

  width: 1000px;
  max-width: 95%;
  margin: 0 auto;
  display: grid;
  align-items: center;
}

.buttons {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  gap: 25px;
}

.link {
  width: 100%;
  height: 100%;
  text-decoration: none;
  align-items: center;
  appearance: none;
  background-color: #fff;
  border-radius: 24px;
  border-style: none;
  box-shadow: rgba(0, 0, 0, .2) 0 3px 5px -1px, rgba(0, 0, 0, .14) 0 6px 10px 0, rgba(0, 0, 0, .12) 0 1px 18px 0;
  box-sizing: border-box;
  color: #3c4043;
  cursor: pointer;
  display: inline-flex;
  fill: currentcolor;
  font-family: "Google Sans", Roboto, Arial, sans-serif;
  font-size: 30px;
  font-weight: 500;
  justify-content: center;
  letter-spacing: .25px;
  line-height: normal;
  max-width: 100%;
  overflow: visible;
  padding: 12px 24px;
  position: relative;
  text-align: center;
  text-transform: none;
  transition: box-shadow 280ms cubic-bezier(.4, 0, .2, 1), opacity 15ms linear 30ms, transform 270ms cubic-bezier(0, 0, .2, 1) 0ms;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  width: auto;
  will-change: transform, opacity;
  z-index: 0;

  &__active {
    background: #cfdfff;
    color: #174ea6;
  }


  &:hover {
    background: #cfdfff;
    color: #174ea6;
  }

  &:active {
    box-shadow: 0 4px 4px 0 rgb(60 64 67 / 30%), 0 8px 12px 6px rgb(60 64 67 / 15%);
    outline: none;
  }

  // &:focus {
  //     outline: none;
  //     border: 2px solid #4285f4;
  // }

  &__test {
    color: #fd0303;
  }
}

.loading {
  position: absolute;
  display: flex;
  width: 100%;
  height: 100%;
  font-size: 30px;
  justify-content: center;
  align-items: center;
}

.loading__text {
  width: 900px;
  max-width: 80%;
  -webkit-animation: blink1 3s linear infinite;
  animation: blink1 3s linear infinite;
}

@-webkit-keyframes blink1 {
  0% {
    color: rgba(34, 34, 34, 1);
  }

  50% {
    color: rgba(34, 34, 34, 0.3);
  }

  100% {
    color: rgba(34, 34, 34, 1);
  }
}

@keyframes blink1 {
  0% {
    color: rgba(34, 34, 34, 1);
  }

  50% {
    color: rgba(34, 34, 34, 0.3);
  }

  100% {
    color: rgba(34, 34, 34, 1);
  }
}


.error__text {
  -webkit-animation: blink2 3s linear infinite;
  animation: blink2 3s linear infinite;
}

@-webkit-keyframes blink2 {
  0% {
    color: rgba(252, 1, 1, 1);
  }

  50% {
    color: rgba(252, 1, 1, 0.5);
  }

  100% {
    color: rgba(252, 1, 1, 1);
  }
}

@keyframes blink2 {
  0% {
    color: rgba(252, 1, 1, 1);
  }

  50% {
    color: rgba(252, 1, 1, 0.5);
  }

  100% {
    color: rgba(252, 1, 1, 1);
  }
}

.info {
  &__container {
    padding: 0;
    margin: 20px 0 20px 60px;
  }

  &__text {
    font-size: 22px;
    font-weight: 400;
    text-align: left;
  }
}